/** {
	border: 1px solid #555;
}*/

a,
body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
  text-decoration: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1170px;
  margin: 0 auto;
}

.header__user-fon {
  background-color: #000;
}

.header__user-bar {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  color: #444;
}

.header__user-bar select {
  background: inherit;
  border: none;
}

.header__user-bar a {
  color: #444;
}

.header__user-bar--social {
  display: flex;
  flex-wrap: wrap;
}

.header__user-bar--user {
  display: flex;
  flex-wrap: wrap;
}

.header__slider {
  text-align: center;
  min-height: 800px;
  background: linear-gradient(rgba(0, 0, 0, 0.77), rgba(0, 0, 0, 0.77)), url("../img/Layer_66.png") no-repeat center / cover;
  position: relative;
  color: #fff;
}

.header__slider a {
  color: #fff;
}

.header__nav {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.header__nav--menu {
  display: flex;
  flex-wrap: wrap;
}

.header__nav--user {
  display: flex;
  flex-wrap: wrap;
}

.sort {
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.12);
  text-transform: uppercase;
  padding: 20px 0;
}

.sort .wrapper {
  display: flex;
  flex-wrap: wrap;
}

.sort__menu {
  display: flex;
  flex-wrap: wrap;
}

.sort__menu li {
  margin-left: 10px;
}

.sort__menu--style {
  color: #666;
  border-radius: 20px;
  padding: 5px 15px;
  background-color: #999;
}

.sort__menu--style:hover,
.sort__menu--style-aktive {
  /*outline: 2px solid blue;
				outline-offset: -2px;*/
  border: 2px solid blue;
  background-color: #fff;
  color: blue;
}

.content {
  background-color: #999;
}

.content .wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

* {
  border: 1px solid blue;
}

.post-space {
  max-width: 820px;
}

.post .autor {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.post .autor__info {
  display: flex;
  flex-wrap: wrap;
}

.post {
  background-color: #fff;
}

.horizont-post .post {
  display: flex;
}

.horizont-post-3 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.horizont-post-3 .post {
  max-width: 260px;
}

.horizont-post-2 {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.horizont-post-2 .post {
  max-width: 400px;
}

.arrow-more {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-end;
  padding: 20px 0;
}

.aside {
  background-color: #fff;
  padding: 30px;
}

.aside p {
  text-align: center;
}

.aside__categories--line {
  display: flex;
  justify-content: space-between;
}

.aside__social {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.aside__widget--menu {
  display: flex;
}

.aside__post {
  display: flex;
}

.aside__post--wrapper {
  flex-grow: 1;
}

.aside__post--info {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.aside__tags--elemets {
  display: flex;
  flex-wrap: wrap;
  max-width: 280px;
}

.aside__tags--elemets li {
  padding: 5px 10px;
  background-color: #ddd;
  margin-right: 10px;
}